/* Footer Component Styles */
.footer {
  background: #2c3e50;
  color: white;
  padding: 50px 0 20px;
}

.footer-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  margin-bottom: 2rem;
}

.footer-section h3 {
  margin-bottom: 1.5rem;
  color: #ecf0f1;
  font-size: 1.2rem;
}

.social-links {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.social-link {
  color: #ecf0f1;
  text-decoration: none;
  padding: 8px 16px;
  border: 1px solid #34495e;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.social-link:hover {
  background: #34495e;
  transform: translateY(-2px);
}

.footer-nav {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.footer-nav a {
  color: #bdc3c7;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-nav a:hover {
  color: #ecf0f1;
}

.footer-bottom {
  border-top: 1px solid #34495e;
  padding-top: 1.5rem;
  text-align: center;
}

.footer-bottom p {
  color: #95a5a6;
  margin: 0.5rem 0;
}

@media (max-width: 768px) {
  .footer-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .social-links {
    justify-content: center;
  }
}

