/* Skills Component Styles */
.skills {
  padding: 100px 0;
  background: white;
}

.skills h2 {
  font-size: 2.5rem;
  margin-bottom: 3rem;
  color: #333;
  text-align: center;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

.skill-item {
  background: #f8f9fa;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.skill-item h3 {
  margin-bottom: 1rem;
  color: #333;
}

.skill-bar {
  background: #e9ecef;
  height: 8px;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 0.5rem;
}

.skill-progress {
  background: linear-gradient(45deg, #667eea, #764ba2);
  height: 100%;
  border-radius: 4px;
}

.skill-percentage {
  font-weight: bold;
  color: #667eea;
}

