/* Hero Component Styles */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  background: var(--gradient-bg);
  padding-top: 80px; /* Évite le masquage par la navbar */
}

/* Background Effects */
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(
      circle at 20% 50%,
      rgba(99, 102, 241, 0.3) 0%,
      transparent 50%
    ),
    radial-gradient(
      circle at 80% 20%,
      rgba(139, 92, 246, 0.3) 0%,
      transparent 50%
    ),
    radial-gradient(
      circle at 40% 80%,
      rgba(6, 182, 212, 0.2) 0%,
      transparent 50%
    );
  animation: float 20s ease-in-out infinite;
}

.hero::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
      45deg,
      transparent 30%,
      rgba(99, 102, 241, 0.1) 50%,
      transparent 70%
    ),
    linear-gradient(
      -45deg,
      transparent 30%,
      rgba(139, 92, 246, 0.1) 50%,
      transparent 70%
    );
  animation: pulse 15s ease-in-out infinite;
}

.hero-container {
  position: relative;
  z-index: 2;
  text-align: center;
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
}

/* Section nom et photo */
.hero-name-section {
  margin-bottom: 2rem;
}

.name-photo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  flex-wrap: nowrap;
  margin-bottom: 3rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

/* Photo placeholder */
.photo-placeholder {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  margin-left: 2rem;
}

.photo-frame {
  width: 100%;
  height: 100%;
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.3) 0%,
    rgba(139, 92, 246, 0.3) 50%,
    rgba(6, 182, 212, 0.3) 100%
  );
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.photo-frame:hover {
  transform: scale(1.05);
  border-color: var(--primary-color);
  box-shadow: 0 10px 40px rgba(99, 102, 241, 0.3);
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--border-radius-full);
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--text-secondary);
  backdrop-filter: blur(10px);
  margin-bottom: 3rem;
  animation: glow 3s ease-in-out infinite;
}

.hero-badge .status-dot {
  width: 8px;
  height: 8px;
  background: var(--success-color);
  border-radius: 50%;
  animation: pulse 2s ease-in-out infinite;
}

.hero-title {
  font-family: var(--font-display);
  font-size: clamp(3rem, 8vw, 6rem);
  font-weight: 700;
  line-height: 1.4;
  margin-bottom: -3rem;
  text-align: left;
  flex: 1;
  background: linear-gradient(
    135deg,
    #f8fafc 0%,
    #e2e8f0 25%,
    #cbd5e1 50%,
    #f8fafc 75%,
    #ffffff 100%
  );
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
  0%,
  100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.hero-subtitle {
  font-size: clamp(1.5rem, 4vw, 2.5rem);
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 1rem;
  opacity: 0.9;
}

.hero-description {
  font-size: clamp(1.1rem, 2vw, 1.3rem);
  color: var(--text-muted);
  max-width: 600px;
  margin: 0 auto 3rem auto;
  line-height: 1.6;
}

.hero-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 4rem;
}

.cta-primary {
  padding: 1rem 2.5rem;
  background: var(--gradient-primary);
  color: white;
  border: none;
  border-radius: var(--border-radius);
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-lg);
  position: relative;
  overflow: hidden;
}

.cta-primary::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  transition: left 0.5s;
}

.cta-primary:hover::before {
  left: 100%;
}

.cta-primary:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-2xl), var(--shadow-glow);
}

.cta-secondary {
  padding: 1rem 2rem;
  background: transparent;
  color: var(--text-primary);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.cta-secondary:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--primary-color);
  transform: translateY(-2px);
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
}

.stat-item {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: 2rem;
  font-weight: 800;
  color: var(--primary-light);
  font-family: var(--font-mono);
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Floating Elements */
.floating-element {
  position: absolute;
  opacity: 0.1;
  pointer-events: none;
}

.floating-element-1 {
  top: 20%;
  left: 10%;
  width: 100px;
  height: 100px;
  background: var(--gradient-primary);
  border-radius: 50%;
  animation: float 8s ease-in-out infinite;
}

.floating-element-2 {
  top: 60%;
  right: 15%;
  width: 80px;
  height: 80px;
  background: var(--gradient-secondary);
  border-radius: 30%;
  animation: float 6s ease-in-out infinite reverse;
}

.floating-element-3 {
  bottom: 20%;
  left: 20%;
  width: 120px;
  height: 60px;
  background: var(--gradient-accent);
  border-radius: var(--border-radius);
  animation: float 10s ease-in-out infinite;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .name-photo-container {
    gap: 2rem;
  }
  
  .hero-title {
    font-size: clamp(2.5rem, 6vw, 4rem);
  }
}

@media (max-width: 768px) {
  .hero {
    padding-top: 100px; /* Plus d'espace sur mobile pour éviter la navbar */
  }
  
  .name-photo-container {
    flex-direction: column;
    gap: 2rem;
    text-align: center;
    flex-wrap: wrap;
  }

  .photo-placeholder {
    width: 150px;
    height: 150px;
    order: -1; /* Place l'image en premier sur mobile */
    margin-left: 0;
    margin: 0 auto;
  }

  .hero-title {
    margin-bottom: 0;
    font-size: clamp(2rem, 5vw, 3rem);
    line-height: 1.2;
    word-break: break-word;
    hyphens: auto;
    text-align: center;
  }

  .hero-actions {
    flex-direction: column;
    gap: 1rem;
  }

  .cta-primary,
  .cta-secondary {
    width: 100%;
    max-width: 280px;
  }

  .hero-stats {
    gap: 2rem;
  }

  .floating-element {
    display: none;
  }
}

@media (max-width: 480px) {
  .hero {
    padding-top: 90px; /* Ajustement pour petits écrans */
  }
  
  .hero-container {
    padding: 1rem;
  }

  .name-photo-container {
    gap: 1.5rem;
  }

  .photo-placeholder {
    width: 120px;
    height: 120px;
    margin: 0 auto;
  }

  .hero-title {
    font-size: clamp(1.5rem, 4vw, 2.5rem);
    line-height: 1.1;
    margin-bottom: 0;
  }

  .hero-badge {
    padding: 0.4rem 0.8rem;
    font-size: 0.8rem;
    margin-bottom: 2rem;
  }

  .hero-description {
    font-size: 1rem;
    margin-bottom: 2rem;
  }

  .hero-stats {
    gap: 1.5rem;
  }

  .stat-number {
    font-size: 1.5rem;
  }
}
